<template>
    <div class="page-container">
        <div class="page-content">
            <div class="breadcrumbs"><a href="/#/">главная</a> / <span class="title">{{ $localizeFilter('outer_page_' + type) }}</span></div>

            <p class="header">{{ $localizeFilter('outer_page_' + type) }}</p>

            <div class="wrapper">
                <div class="card-container">
                    <component :is="cardComponent" v-for="(card, index) in cards" :key="index" :card="card"></component>
                </div>
            </div>

            <pagination v-model="page" :records="count" :per-page="pageSize" @paginate="pageChangeHandler"/>
        </div>
        
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

import paginationMixin from '@/mixins/pagination.mixin'

import RealEstateCard from '@/components/cards/RealEstateCard.vue'
import VideosAboutUsCard from '@/components/cards/VideosAboutUsCard.vue'
import CharityCard from '@/components/cards/CharityCard.vue'
import StartupsCard from '@/components/cards/StartupsCard.vue'
import NewsAboutUsCard from '@/components/cards/NewsAboutUsCard.vue'




// import ReplenishSigma from "@/components/modals/ReplenishSigma.vue"
// import ReplenishWallet from "@/components/modals/ReplenishWallet.vue"
// import AttentionWallet from "@/components/modals/AttentionWallet.vue"
// import WithdrawalSigma from "@/components/modals/WithdrawalSigma.vue"
// import WithdrawalWallet from "@/components/modals/WithdrawalWallet.vue"
// import PaymentSystemChoiceWallet from "@/components/modals/PaymentSystemChoiceWallet.vue"
// import InternalTransfer from "@/components/modals/InternalTransfer.vue"
// import StructureClientProfile from "@/components/modals/StructureClientProfile.vue"
// import ConfirmAction from "@/components/modals/ConfirmAction.vue"

function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}

export default defineComponent({
    components: {
        RealEstateCard, VideosAboutUsCard, CharityCard, StartupsCard, NewsAboutUsCard
    },
    mixins: [paginationMixin],
    watch:{
        page() {
            this.$nextTick(this.loadData);
        },
        type() {
            this.$nextTick(this.loadData);
        },
    },
    setup(props) {

    },
    data: () => ({
        cards: [],
        count: 0,
    }),
    props: {
        type: { // realEstate, videosAboutUs
            type: String, 
            required: true
        },
    },
    computed: {
        cardComponent() {
            return capitalizeFirstLetter(this.type)+'Card'
        }
    },
    mounted() {
        this.loadData()
    },
    methods: {
        async loadData() {
            try {
                const data = {type: this.type, skip: this.skip, limit: this.limit}
                this.cards = await this.$store.dispatch('getArticlesByFilter', data)
                const {count} = await this.$store.dispatch('getArticlesByFilterCount', data)
                this.count = count
                console.log(this.cards, this.count)
                this.setupPagination(this.cards)
            } catch(e) {
                this.$error(e)
            } 
        }
    }
})
</script>

